import React from 'react';
import './style.scss';

const PurpleBackground = (props) => {
  return (
    <div className="purple-background">
      <div className="purple-background__container">
        <div className="purple-background__svg-container">
          <img className="purple-background__hexagon" alt="hexagon" src="data:image/svg+xml;base64,"/>
          <img className="purple-background__hexagon-inner" alt="hexagon-inner" src={props.hexagonInner} />
        </div>
        <p className="purple-background__paragraph">{props.paragraphInner}</p>
      </div>
    </div>
  );
};

export default PurpleBackground;